<template>
  <div>
    <div class="mine box flex ac">
      <CustomImg class="portait" width="1.33rem" height="1.33rem" :src="list.head_pic" round></CustomImg>
      <div>
        <div class="nickname flex ac">
          {{list.nickname | longtext}}
          <img :src="require('@/assets/gold.png')" />
        </div>
        <div class="nums">团队人数：{{list.team_count}}</div>
      </div>
    </div>
    <Notice
      v-if="list.audit_count && taskVisible"
      class="box"
      text="团长提交了社群任务，请审核"
      @click.native="toOther('/task')"
    ></Notice>
    <div class="info box">
      <div class="info-title">基础资料</div>
      <div class="info-item flex ac js">
        <div>手机号</div>
        <div>{{list.mobile}}</div>
      </div>
      <div class="info-item flex ac js">
        <div>注册时间</div>
        <div>{{list.reg_time}}</div>
      </div>
      <div v-if='list.join_time' class="info-item flex ac js">
        <div>加入团队时间</div>
        <div>{{list.join_time}}</div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import { getMemberInfo } from "@/api";
import Notice from "@/components/Notice";

export default {
  components: { Notice },
  data() {
    return {
      list: {}
    };
  },
  computed: {
    ...mapGetters({taskVisible: 'config/taskVisible'}),
  },
  created() {
    const { user_id, nickname, head_pic } = this.$route.query;
    this.list = {
      nickname,
      head_pic
    };
    this.getData(user_id);
    this.$store.dispatch("config/getConfig");
  },
  methods: {
    getData(user_id) {
      getMemberInfo({ user_id }).then(res => {
        if (res.success) {
          this.list = {
            ...this.list,
            ...res.data
          };
        }
      });
    },
    // 跳转
    toOther(path) {
      this.$router.push(path);
    }
  }
};
</script>

<style lang="less" scoped>
.box {
  margin: 10px 13px;
  background: rgba(255, 255, 255, 1);
}

.mine {
  background: #fff;
  border-radius: 8px;
}

.portait {
  margin: 15px 10px 15px 13px;
}

.nickname {
  color: #282828;
  font-size: 14px;
  line-height: 20px;
  img {
    width: 14px;
    margin-left: 5px;
  }
}

.nums {
  color: #777;
  font-size: 12px;
  margin-top: 5px;
  line-height: 17px;
}

.info {
  overflow: hidden;
  padding: 0 10px;
}

.info-title {
  color: #282828;
  font-size: 16px;
  margin-top: 10px;
  line-height: 23px;
}

.info-item {
  padding: 30px 0;
  color: #666;
  font-size: 14px;
  border-bottom: 1px solid #d8d8d8;
  &:last-child {
    border-bottom: none;
  }
}
</style>